import React, { useState } from 'react';

interface Props {
    id : number;
    expanded : boolean;
}

const PropListBody: React.FC<Props> = (props) => {
    let cn;
    if (props.expanded) {
        cn = 'body.expanded';
    } else {
        cn = 'body'
    }
    return <div className={cn}>
        <div style={{height : "500px"}}></div>
    </div>
};

export default PropListBody;